import React, { Component, Fragment } from 'react';
import URL_API from '../../assets/conf';
import { Grid, Row } from 'rsuite';
import Element from '../elements/Element';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';
import ChartHorizontalBar100 from '../charts/HorizontalBar100';
import Error from '../elements/error';

const PersonalEl = (props) =>{
    return(
        <Element title={props.title} md={12} fill={1}>
            <ChartBar
                label="Registros"
                width={200}
                data={[
                    props.data._1_, 
                    props.data._2_, 
                    props.data._3_, 
                    props.data._4_, 
                    props.data.nan
                ]} 
                labels={[`0 ${props.personal}`, `1-2 ${props.abr}.`, `3-4 ${props.abr}.`, `5+ ${props.abr}.`, "Sin resp."]}
            />
        </Element>
    );
}

class SecUnidadSalud extends Component{
    state ={
        _group_: this.props.group,
        cargar: false,
        error: false,
        length: 0,
        tElectricidad: {},
        fuenteAgua: {},
        tipoServicioSanitario: {},
        ppMedicoBase: {},
        ppMedicoEventual: {},
        ppEnfermeriaBase: {},
        ppEnfermeriaEventual: {},
        ppAuxiliarSalud: {},
        ppPromotorSalud: {},
        ppComunitario: {},
        ppEspecialistaBase: {},
        ppEspecialistaEventual: {},
        ppOtro: {},
        acu: {},
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/nutres/seccion/encargado/${this.props.group}`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;
                
                return {
                    tElectricidad: await data.map(e =>{  return parseInt(e.tElectricidad)  }),
                    fuenteAgua: await data.map(e =>{  return parseInt(e.fuenteAgua)  }),
                    tipoServicioSanitario: await data.map(e =>{  return parseInt(e.tipoServicioSanitario)  }),
                    ppMedicoBase: await data.map(e =>{  return parseInt(e.ppMedicoBase)  }),
                    ppMedicoEventual: await data.map(e =>{  return parseInt(e.ppMedicoEventual)  }),
                    ppEnfermeriaBase: await data.map(e =>{  return parseInt(e.ppEnfermeriaBase)  }),
                    ppEnfermeriaEventual: await data.map(e =>{  return parseInt(e.ppEnfermeriaEventual)  }),
                    ppAuxiliarSalud: await data.map(e =>{  return parseInt(e.ppAuxiliarSalud)  }),
                    ppPromotorSalud: await data.map(e =>{  return parseInt(e.ppPromotorSalud)  }),
                    ppComunitario: await data.map(e =>{  return parseInt(e.ppComunitario)  }),
                    ppEspecialistaBase: await data.map(e =>{  return parseInt(e.ppEspecialistaBase)  }),
                    ppEspecialistaEventual: await data.map(e =>{  return parseInt(e.ppEspecialistaEventual)  }),
                    ppOtro: await data.map(e =>{  return parseInt(e.ppOtro)  }),
                    acu: {
                        a: await data.map(e =>{  return parseInt(e.acu_a)  }),
                        b: await data.map(e =>{  return parseInt(e.acu_b)  }),
                        c: await data.map(e =>{  return parseInt(e.acu_c)  }),
                        d: await data.map(e =>{  return parseInt(e.acu_d)  }),
                        e: await data.map(e =>{  return parseInt(e.acu_e)  }),
                        f: await data.map(e =>{  return parseInt(e.acu_f)  }),
                        g: await data.map(e =>{  return parseInt(e.acu_g)  }),
                        h: await data.map(e =>{  return parseInt(e.acu_h)  }),
                        i: await data.map(e =>{  return parseInt(e.acu_i)  }),
                        j: await data.map(e =>{  return parseInt(e.acu_j)  }),
                        k: await data.map(e =>{  return parseInt(e.acu_k)  }),
                        l: await data.map(e =>{  return parseInt(e.acu_l)  }),
                        m: await data.map(e =>{  return parseInt(e.acu_m)  }),
                        n: await data.map(e =>{  return parseInt(e.acu_n)  }),
                        o: await data.map(e =>{  return parseInt(e.acu_o)  }),
                        p: await data.map(e =>{  return parseInt(e.acu_p)  }),
                        q: await data.map(e =>{  return parseInt(e.acu_q)  }),
                        r: await data.map(e =>{  return parseInt(e.acu_r)  }),
                        s: await data.map(e =>{  return parseInt(e.acu_s)  }),
                        t: await data.map(e =>{  return parseInt(e.acu_t)  }),
                        u: await data.map(e =>{  return parseInt(e.acu_u)  }),
                        v: await data.map(e =>{  return parseInt(e.acu_v)  }),
                    },
                }
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDataAll();

        if (data !== 'error')
        {
            this.setState({
                length: data.tElectricidad.length,
                tElectricidad: {
                    si: await data.tElectricidad.filter(e => e === 1).length,
                    no: await data.tElectricidad.filter(e => e === 2).length,
                },
                fuenteAgua: {
                    _1_: await data.fuenteAgua.filter(e => e === 1).length,
                    _2_: await data.fuenteAgua.filter(e => e === 2).length,
                    _3_: await data.fuenteAgua.filter(e => e === 3).length,
                    _1111_: await data.fuenteAgua.filter(e => e === 1111).length,
                    _8888_: await data.fuenteAgua.filter(e => e === 8888).length,
                },
                tipoServicioSanitario: {
                    _1_: await data.tipoServicioSanitario.filter(e => e === 1).length,
                    _2_: await data.tipoServicioSanitario.filter(e => e === 2).length,
                    _3_: await data.tipoServicioSanitario.filter(e => e === 3).length,
                    _1111_: await data.tipoServicioSanitario.filter(e => e === 1111).length,
                    _8888_: await data.tipoServicioSanitario.filter(e => e === 8888).length,
                },
                ppMedicoBase: {
                    _1_ : await data.ppMedicoBase.filter(e => e === 0).length,
                    _2_ : await data.ppMedicoBase.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppMedicoBase.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppMedicoBase.filter(e => e >= 5).length,
                    nan : await data.ppMedicoBase.filter(e => isNaN(e)).length,
                },
                ppMedicoEventual: {
                    _1_ : await data.ppMedicoEventual.filter(e => e === 0).length,
                    _2_ : await data.ppMedicoEventual.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppMedicoEventual.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppMedicoEventual.filter(e => e >= 5).length,
                    nan : await data.ppMedicoEventual.filter(e => isNaN(e)).length,
                },
                ppEnfermeriaBase: {
                    _1_ : await data.ppEnfermeriaBase.filter(e => e === 0).length,
                    _2_ : await data.ppEnfermeriaBase.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppEnfermeriaBase.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppEnfermeriaBase.filter(e => e >= 5).length,
                    nan : await data.ppEnfermeriaBase.filter(e => isNaN(e)).length,
                },
                ppEnfermeriaEventual: {
                    _1_ : await data.ppEnfermeriaEventual.filter(e => e === 0).length,
                    _2_ : await data.ppEnfermeriaEventual.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppEnfermeriaEventual.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppEnfermeriaEventual.filter(e => e >= 5).length,
                    nan : await data.ppEnfermeriaEventual.filter(e => isNaN(e)).length,
                },
                ppAuxiliarSalud: {
                    _1_ : await data.ppAuxiliarSalud.filter(e => e === 0).length,
                    _2_ : await data.ppAuxiliarSalud.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppAuxiliarSalud.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppAuxiliarSalud.filter(e => e >= 5).length,
                    nan : await data.ppAuxiliarSalud.filter(e => isNaN(e)).length,
                },
                ppPromotorSalud: {
                    _1_ : await data.ppPromotorSalud.filter(e => e === 0).length,
                    _2_ : await data.ppPromotorSalud.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppPromotorSalud.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppPromotorSalud.filter(e => e >= 5).length,
                    nan : await data.ppPromotorSalud.filter(e => isNaN(e)).length,
                },
                ppComunitario: {
                    _1_ : await data.ppComunitario.filter(e => e === 0).length,
                    _2_ : await data.ppComunitario.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppComunitario.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppComunitario.filter(e => e >= 5).length,
                    nan : await data.ppComunitario.filter(e => isNaN(e)).length,
                },
                ppEspecialistaBase: {
                    _1_ : await data.ppEspecialistaBase.filter(e => e === 0).length,
                    _2_ : await data.ppEspecialistaBase.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppEspecialistaBase.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppEspecialistaBase.filter(e => e >= 5).length,
                    nan : await data.ppEspecialistaBase.filter(e => isNaN(e)).length,
                },
                ppEspecialistaEventual: {
                    _1_ : await data.ppEspecialistaEventual.filter(e => e === 0).length,
                    _2_ : await data.ppEspecialistaEventual.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppEspecialistaEventual.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppEspecialistaEventual.filter(e => e >= 5).length,
                    nan : await data.ppEspecialistaEventual.filter(e => isNaN(e)).length,
                },
                ppOtro: {
                    _1_ : await data.ppOtro.filter(e => e === 0).length,
                    _2_ : await data.ppOtro.filter(e => e >= 1 && e <= 2).length,
                    _3_ : await data.ppOtro.filter(e => e >= 3 && e <= 4).length,
                    _4_ : await data.ppOtro.filter(e => e >= 5).length,
                    nan : await data.ppOtro.filter(e => isNaN(e)).length,
                },
                acu: {
                    a: await data.acu.a.filter(e => e >= 1 && e <= 8).length,
                    b: await data.acu.b.filter(e => e >= 1 && e <= 8).length,
                    c: await data.acu.c.filter(e => e >= 1 && e <= 8).length,
                    d: await data.acu.d.filter(e => e >= 1 && e <= 8).length,
                    e: await data.acu.e.filter(e => e >= 1 && e <= 8).length,
                    f: await data.acu.f.filter(e => e >= 1 && e <= 8).length,
                    g: await data.acu.g.filter(e => e >= 1 && e <= 8).length,
                    h: await data.acu.h.filter(e => e >= 1 && e <= 8).length,
                    i: await data.acu.i.filter(e => e >= 1 && e <= 8).length,
                    j: await data.acu.j.filter(e => e >= 1 && e <= 8).length,
                    k: await data.acu.k.filter(e => e >= 1 && e <= 8).length,
                    l: await data.acu.l.filter(e => e >= 1 && e <= 8).length,
                    m: await data.acu.m.filter(e => e >= 1 && e <= 8).length,
                    n: await data.acu.n.filter(e => e >= 1 && e <= 8).length,
                    o: await data.acu.o.filter(e => e >= 1 && e <= 8).length,
                    p: await data.acu.p.filter(e => e >= 1 && e <= 8).length,
                    q: await data.acu.q.filter(e => e >= 1 && e <= 8).length,
                    r: await data.acu.r.filter(e => e >= 1 && e <= 8).length,
                    s: await data.acu.s.filter(e => e >= 1 && e <= 8).length,
                    t: await data.acu.t.filter(e => e >= 1 && e <= 8).length,
                    u: await data.acu.u.filter(e => e >= 1 && e <= 8).length,
                    v: await data.acu.v.filter(e => e >= 1 && e <= 8).length,
                },
                cargar: true
            });

        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }
    
    componentDidUpdate = async () =>{
        if (this.props.group !== this.state._group_)
        {
            this.setState({
                cargar: false,
                _group_: this.props.group
            });
            
            await this.handleGetData();
        }
    }

    render(){
        if (this.state.error){
            return <Error />
        }
        
        // if (!this.state.cargar){
        //     return <h1>Cargando...</h1>
        // }

        return (
            <Fragment>
                <Grid fluid style={{margin:0, padding:0}}>

                    {/* Graficas de Energia y Fuente de agua */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="¿La US cuenta con energía eléctrica?" md={12} fill={1}>
                                <ChartPie
                                    width={200}
                                    labels={["Si", "No"]}
                                    data={[this.state.tElectricidad.si, this.state.tElectricidad.no]}
                                />
                            </Element>
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <Element title="Tipo de fuente de abastecimiento de agua" md={12} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    data={[this.state.fuenteAgua._1_, this.state.fuenteAgua._2_, this.state.fuenteAgua._3_, this.state.fuenteAgua._1111_, this.state.fuenteAgua._8888_]} 
                                    labels={["Entubada", "En pipas", "Pozo o manantial", "Otro", "No sabe"]}
                                />
                            </Element>
                            :<Element md={12}/>
                        }
                    </Row>

                    {/* Graficas de Servicios sanitarios */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Tipo de servicio sanitario" md={24} fill={1}>
                                <ChartHorizontalBar
                                    label="Registros"
                                    width={200}
                                    data={[
                                        this.state.tipoServicioSanitario._1_, 
                                        this.state.tipoServicioSanitario._2_, 
                                        this.state.tipoServicioSanitario._3_, 
                                        this.state.tipoServicioSanitario._1111_, 
                                        this.state.tipoServicioSanitario._8888_
                                    ]} 
                                    labels={[
                                        "Excusado conectado al sistema de dreanaje", 
                                        "Excusado conectado a fosa séptica", 
                                        "Letrina", 
                                        "Otro", 
                                        "No sabe"
                                    ]}
                                />
                            </Element>
                            :<Element md={24}/>
                        }
                    </Row>

                    <span className="title-section">Personal en los últimos 6 meses</span>
                    
                    {/* Graficos de medicos base y eventuales */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Medic@s de base"
                            data={this.state.ppMedicoBase}
                            personal="Medic@s" abr="M"
                            />
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Medic@s eventualales"
                            data={this.state.ppMedicoEventual}
                            personal="Medic@s" abr="M"
                            />
                            :<Element md={12}/>
                        }
                    </Row>
                    
                    {/* Graficos de personal de enfermeria base y eventuales */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Personal de enfermería de base"
                            data={this.state.ppEnfermeriaBase}
                            personal="Perso." abr="P"
                            />
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Personal de enfermería eventual"
                            data={this.state.ppEnfermeriaEventual}
                            personal="Perso." abr="P"
                            />
                            :<Element md={12}/>
                        }
                    </Row>

                    {/* Graficos de personal auxiliar y promotores de salud */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Auxiliares de salud"
                            data={this.state.ppAuxiliarSalud}
                            personal="Auxi." abr="A"
                            />
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Promotores de salud"
                            data={this.state.ppPromotorSalud}
                            personal="Promo." abr="P"
                            />
                            :<Element md={12}/>
                        }
                    </Row>

                    {/* Graficos de especialistas base y eventuales */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Especialistas de base"
                            data={this.state.ppEspecialistaBase}
                            personal="Especia." abr="E"
                            />
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <PersonalEl
                            title="Especialistas eventuales"
                            data={this.state.ppEspecialistaEventual}
                            personal="Especia." abr="E"
                            />
                            :<Element md={12}/>
                        }
                    </Row>

                    {/* Graficos de personal comunitario y otros */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Personal comunitario"
                            data={this.state.ppComunitario}
                            personal="Perso." abr="P"
                            />
                            : <Element md={12} />
                        }
                        { this.state.cargar ?
                            <PersonalEl 
                            title="Otro tipo de personal"
                            data={this.state.ppOtro}
                            personal="Otros t." abr="O."
                            />
                            :<Element md={12}/>
                        }
                    </Row>

                    <span className="title-section">Áreas en funcionamiento</span>

                    {/* Graficas de Areas */}
                    <Row className="box-element-container">
                        { this.state.cargar ?
                            <Element title="Áreas en funcionamiento" md={24} fill={1}>
                                <ChartHorizontalBar100 
                                height={1000}
                                msgY="Unidades de salud"
                                data={[
                                    {
                                        name: "1-6 Áreas/7+ Áreas/Área compartida",
                                        data: [
                                            this.state.acu.a,
                                            this.state.acu.b,
                                            this.state.acu.c,
                                            this.state.acu.d,
                                            this.state.acu.e,
                                            this.state.acu.f,
                                            this.state.acu.g,
                                            this.state.acu.h,
                                            this.state.acu.i,
                                            this.state.acu.j,
                                            this.state.acu.k,
                                            this.state.acu.l,
                                            this.state.acu.m,
                                            this.state.acu.n,
                                            this.state.acu.o,
                                            this.state.acu.p,
                                            this.state.acu.q,
                                            this.state.acu.r,
                                            this.state.acu.s,
                                            this.state.acu.t,
                                            this.state.acu.u,
                                            this.state.acu.v,
                                        ]
                                    },
                                    {
                                        name: "Ningun@ área/No sabe/Sin resp.",
                                        data: [
                                            this.state.length - this.state.acu.a,
                                            this.state.length - this.state.acu.b,
                                            this.state.length - this.state.acu.c,
                                            this.state.length - this.state.acu.d,
                                            this.state.length - this.state.acu.e,
                                            this.state.length - this.state.acu.f,
                                            this.state.length - this.state.acu.g,
                                            this.state.length - this.state.acu.h,
                                            this.state.length - this.state.acu.i,
                                            this.state.length - this.state.acu.j,
                                            this.state.length - this.state.acu.k,
                                            this.state.length - this.state.acu.l,
                                            this.state.length - this.state.acu.m,
                                            this.state.length - this.state.acu.n,
                                            this.state.length - this.state.acu.o,
                                            this.state.length - this.state.acu.p,
                                            this.state.length - this.state.acu.q,
                                            this.state.length - this.state.acu.r,
                                            this.state.length - this.state.acu.s,
                                            this.state.length - this.state.acu.t,
                                            this.state.length - this.state.acu.u,
                                            this.state.length - this.state.acu.v,
                                        ]
                                    }
                                ]}
                                labels={[
                                    "Consultorio para la atención médica y exploración",
                                    "Espacio de enfermería",
                                    "Sala de espera",
                                    "Sala/Área para talleres de salud",
                                    "Área para almacenamiento de medicamentos e insumos",
                                    "Almacenamiento en red fría de medicamentos",
                                    "Almacenamiento en red fría de vacunas",
                                    "Sala de labor",
                                    "Baño para personal",
                                    "Baño para pacientes",
                                    "Baño compartido (pacientes/personal)",
                                    "Farmacia",
                                    "Cuarto de curaciones",
                                    "Sala de expulsión",
                                    "Área de internamiento (camas)",
                                    "Dormitorio para personal",
                                    "Almacén para desechos biológicos",
                                    "Sala de urgencias",
                                    "Quirófano de cirugía menor",
                                    "Laboratorio de análisis clínicos",
                                    "Cuarto de inmunizaciones",
                                    "Cuarto de limpieza",
                                ]}
                                />
                            </Element>
                            :<Element md={24}/>
                        }
                    </Row>

                </Grid>
            </Fragment>
        );
    }
}

export default SecUnidadSalud;